@-webkit-keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.fade-in {
  -webkit-animation-name: fade-in;
          animation-name: fade-in;
  -webkit-animation-duration: 500ms;
          animation-duration: 500ms;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}

@-webkit-keyframes AnimationName {
  0% {
    background-position: 49% 0%;
  }
  50% {
    background-position: 52% 100%;
  }
  100% {
    background-position: 49% 0%;
  }
}

@keyframes AnimationName {
  0% {
    background-position: 49% 0%;
  }
  50% {
    background-position: 52% 100%;
  }
  100% {
    background-position: 49% 0%;
  }
}

.img-placeholder {
  background: -webkit-linear-gradient(315deg, #ffffff, #d3d3d3);
  background: -o-linear-gradient(315deg, #ffffff, #d3d3d3);
  background: linear-gradient(135deg, #ffffff, #d3d3d3);
  background-size: 200% 200%;
  -webkit-animation: AnimationName 1500ms ease-in-out infinite;
          animation: AnimationName 1500ms ease-in-out infinite;
  width: 100%;
  height: 100%;
  border-radius: 6px;
  /* border: 1px solid #f1f1f1; */
  -webkit-box-shadow: 0 3px 20px 0 rgba(0, 0, 0, 0.05), 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 0 1px 0 rgba(0, 0, 1, 0.1);
          box-shadow: 0 3px 20px 0 rgba(0, 0, 0, 0.05), 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 0 1px 0 rgba(0, 0, 1, 0.1);
}



